.bubble {
    width: 25px;
    height: 25px;
    border-radius: 15px;
    border: 2px solid #1990FF;
    background-color: white;
    -webkit-appearance: none; /*to disable the default appearance of radio button*/
    -moz-appearance: none;
    cursor: pointer;
}

.disabled{
    background-color: #F5F5F5;
    border-color: #DADADA;
    cursor: no-drop;
}

.disabled-text {
    color: grey !important;
    cursor: no-drop !important;
}

input[type="radio"]:focus { /*no need, if you don't disable default appearance*/
    outline: none; /*to remove the square border on focus*/
}

.selected{ /*no need, if you don't disable default appearance*/
    background-color: #1990FF;
}

.selected.disabled {
    background-color: #DADADA !important;
}

.selected ~ span:first-of-type {
    color: white;
}

label span:first-of-type {
    position: relative;
    left: -17.5px;
    top: -8px;
    font-size: 15px;
    text-align: center;
    color: #1990FF;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    cursor: pointer;
}

label {
    margin: -4px;
}

.question-number {
    position: relative;
    text-align: right;
    bottom: 8px;
    font-size: 25px;
    margin-right: 10px;
    vertical-align: middle;
    color: grey;
}

.bookmark {
    position: relative;
    text-align: center;
    font-size: 20px;
    vertical-align: middle;
    cursor: pointer;
}

.bookmark.disabled-bookmark {
    cursor: no-drop !important;
    color: #DADADA;
}